import ToolPanelBase from 'ToolPanelBase';
import dat from 'dat-gui'

export default class ParticlePanel extends ToolPanelBase {
  
  constructor(particleTool, viewer, btnElement) {
    
    super(viewer.container, 'Particle Controls', {
      buttonElement: btnElement,
      shadow: true
    });

    this._viewer = viewer;

    this._selectedObject = null;
    
    $(this.container).addClass('particle');

    var gui = new dat.GUI({
      autoPlace: false
    });

    var guiContainer = document.getElementById(
      this.container.id + '-gui');

    guiContainer.appendChild(
      gui.domElement);

    var f = gui.addFolder('Particle System Properties');

    f.add(particleTool.particleSystem,
      'maxParticles', 10, 10000).name(
        'Maximum Particles');

    var nbParticleTypes = f.add(particleTool,
      'nbParticleTypes', 1, 200).name(
        'Particle Types');

    nbParticleTypes.onFinishChange(function(nbTypes) {
      particleTool.setParticleTypes(
        Math.max(1,Math.floor(nbTypes)));
    });

    f.open();

    setTimeout(()=> {
      gui.domElement.style.width = '100%';
    }, 0);
  }
  
  /////////////////////////////////////////////////////////////
  //
  //
  /////////////////////////////////////////////////////////////
  htmlContent(id) {
    
    return `
     <div class="container">
          <div id="${id}-gui">
          </div>
      </div>`;
  }

  /////////////////////////////////////////////////////////////
  //
  //
  /////////////////////////////////////////////////////////////
  htmlContent(id) {

    return `
     <div class="container">
          <div id="${id}-gui">
          </div>
      </div>`;
  }

  /////////////////////////////////////////////////////////////
  //
  //
  /////////////////////////////////////////////////////////////
  loadObjectGUI(obj){

    $('#' + this.container.id + '-obj-gui').remove();

    if(!obj || !obj.selectable){
      return;
    }

    var gui = new dat.GUI({
      autoPlace: false
    });

    $('#' + this.container.id + '-gui').append(
      `<div id="${this.container.id}-obj-gui"></div>`
    );

    var guiContainer = document.getElementById(
      this.container.id + '-obj-gui');

    guiContainer.appendChild(
      gui.domElement);

    switch(obj.type){

      case 'field.magnetic':

        var f = gui.addFolder('Selected Field Properties');
        f.add(obj, 'force', -100, 100).name('Force');
        f.open();
        break;

      case 'emitter':

        var f = gui.addFolder('Selected Emitter Properties');
        f.add(obj, 'emissionRate', 10, 1000).name('Emission Rate');
        f.add(obj, 'spread', 0.0, 6 * Math.PI/180).name('Spread');
        f.add(obj, 'velocity', 1, 10).name('Particles Velocity');
        f.add(obj, 'charge', -10, 10).name('Particles Charge');
        f.open();
        break;

      default:
        break;
    }

    setTimeout(()=> {
      gui.domElement.style.width = '100%';
    }, 0);

    this.setVisible(true);
  }

  /////////////////////////////////////////////////////////////
  //
  //
  /////////////////////////////////////////////////////////////
  setSelected(obj){

    this.loadObjectGUI(obj);
  }
}

var css = `

.particle{
  top: 10px;
  right: 10px;
  width: 300px;
  height: 350px;
  resize: auto;
}

.particle .container {
  width: 100%;
  padding: 0;
}

.particle .btn {
  height: 6px;
  line-height: 0;
  vertical-align: top;
}

.particle .btn-span {
  margin-right: 0px;
  line-height: 0.3;
  top: 1px;
}

.particle .dg.main {
  width: 100%
}

.particle .dg.main.taller-than-window .close-button {
    border-top: 1px solid #ddd;
}

.particle .dg.main .close-button {
    background-color: #ccc;
    display: none;
}

.particle .dg.main .close-button:hover {
    background-color: #ddd;
}

.particle .dg {
    color: #555;
    text-shadow: none !important;
}

.particle .dg.main::-webkit-scrollbar {
    background: #fafafa;
}

.particle .dg.main::-webkit-scrollbar-thumb {
    background: #bbb;
}

.particle .dg li:not(.folder) {
    background: #fafafa;
    border-bottom: 1px solid #ddd;
}

.particle .dg li.save-row .button {
    text-shadow: none !important;
}

.particle .dg li.title {
    background: #e8e8e8 url(data:image/gif;base64,R0lGODlhBQAFAJEAAP////Pz8////////yH5BAEAAAIALAAAAAAFAAUAAAIIlI+hKgFxoCgAOw==) 6px 10px no-repeat;
}

.particle .dg .cr.function:hover,.dg .cr.boolean:hover {
    background: #fff;
}

.particle .dg .c input[type=text] {
    background: #e9e9e9;
    border: 0;
    margin-top: 0px;
    padding: 0px;
    float: right;
}

.particle .dg .c input[type=text]:hover {
    background: #eee;
}

.particle .dg .c input[type=text]:focus {
    background: #eee;
    color: #555;
}

.particle .dg .c .slider {
    background: #e9e9e9;
    height: 28px;
    margin-top: 0px;
}

.particle .dg .c .slider:hover {
    background: #eee;
}
`;

$('<style type="text/css">' + css + '</style>').appendTo('head');


